import { component } from 'picoapp';

function RewardsNav(node){
  node.onclick = (e) => {
    e.preventDefault();
    var rewardsIframe = document.querySelector("iframe[title='Smile.io Rewards Program Launcher']")
    rewardsIframe.scrollIntoView();
    if (window.innerWidth < 700 ){  
      rewardsIframe.contentWindow.document.querySelector('.launcher-button').click();
    } else { 
      window.addEventListener('scroll', openRewards)
    }

    function openRewards(){
      if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight) {
        rewardsIframe.contentWindow.document.querySelector('.launcher-button').click();
        window.removeEventListener('scroll', openRewards);
      }
    }
  } 
} 

export default component((node, ctx) => {
  new RewardsNav(node, ctx);
});
